.Spinner__Loader__1DDwY,
.Spinner__Loader__1DDwY:before,
.Spinner__Loader__1DDwY:after {
    border-radius: 50%;
}

.Spinner__Loader__1DDwY {
    color: #75a5ce;
    font-size: 11px;
    text-indent: -99999em;
    margin: 55px auto;
    position: relative;
    width: 10em;
    height: 10em;
    box-shadow: inset 0 0 0 1em;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}

.Spinner__Loader__1DDwY:before,
.Spinner__Loader__1DDwY:after {
    position: absolute;
    content: '';
}

.Spinner__Loader__1DDwY:before {
    width: 5.2em;
    height: 10.2em;
    background: #fff;
    border-radius: 10.2em 0 0 10.2em;
    top: -0.1em;
    left: -0.1em;
    -webkit-transform-origin: 5.2em 5.1em;
    transform-origin: 5.2em 5.1em;
    -webkit-animation: Spinner__load2__1N7mg 2s infinite 1.5s;
    animation: Spinner__load2__1N7mg 2s infinite ease 1.5s;
}

.Spinner__Loader__1DDwY:after {
    width: 5.2em;
    height: 10.2em;
    background: #fff;
    border-radius: 0 10.2em 10.2em 0;
    top: -0.1em;
    left: 5.1em;
    -webkit-transform-origin: 0px 5.1em;
    transform-origin: 0px 5.1em;
    -webkit-animation: Spinner__load2__1N7mg 2s infinite ease;
    animation: Spinner__load2__1N7mg 2s infinite ease;
  }
  @-webkit-keyframes Spinner__load2__1N7mg {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes Spinner__load2__1N7mg {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
